import React, { Component } from 'react';
import Link from 'gatsby-link';
import Img from 'gatsby-image';
import get from 'lodash/get';
import Helmet from 'react-helmet';
class IndexPage extends Component {
  render() {
    const posts = get(this, 'props.data.allMarkdownRemark.edges');

    return (

      <div className="pc-main-wrapper">

        <div data-aos="fade-left" data-aos-duration="1000" className="pc-border"></div>
        <h1 data-aos="fade-in" data-aos-duration="1000" className="text-center">latest thoughts</h1>
        <div data-aos="fade-right" data-aos-duration="1000" className="pc-border"></div>
        <h2 data-aos="fade-in" data-aos-duration="500" className="text-center">
          <blockquote>
            "The art challenges the technology, and the technology inspires the art. - John Lasseter"
          </blockquote>

        </h2>
        <div className="posts-wrapper">
          {posts.map(({ node }) => {
            const title = get(node, 'frontmatter.title') || node.fields.slug
            return (
              <div className="pc-list-post" key={node.id}>
                <div data-aos="fade" data-aos-duration="1000">
                  <div className="pc-blog-post">
                    <div className="pc-blog-post-image">
                      <Img sizes={node.frontmatter.featuredImage.childImageSharp.sizes} />
                    </div>
                    <div className="pc-blog-post-caption">
                      <Link to={node.frontmatter.path}>
                        <h4>{title}</h4>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}

export const pageQuqery = graphql`
  query IndexQuery{
    allMarkdownRemark(
      limit: 6
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { published: { eq: true } } }) {
      edges {
        node {
          id
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
            path
            featuredImage {
              childImageSharp{
                  sizes(maxWidth: 350) {
                      ...GatsbyImageSharpSizes
                  }
              }
            }
          }
        }
      }
    }
  }
`

export default IndexPage


